.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #e5e5e5;
  font-weight: 600;
  text-transform: uppercase;
  color: #1d1d1d;

  overflow: auto;

  @include onDescktop {
    font-weight: 700;
  }

  &__top {
    margin-bottom: 24px;
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: #e5e5e5;

    @include onTablet {
      margin-bottom: 8px;
    }

    @include onDescktop {
      margin-bottom: 24px;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    line-height: 27px;
    color: inherit;
    text-decoration: none;
  }

  &__call-to-test-ride {
    display: block;
    width: fit-content;

    padding-bottom: 7px;
    border-bottom: 1px solid #1d1d1d;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
  }
}
